import React from 'react';

import FormContainer from '../../../Containers/UIElement/Forms/FormContainer/FormContainerContainer';
import Field from '../../../Containers/UIElement/Forms/Field/FieldContainer';
import TextArea from '../../../Containers/UIElement/Forms/Field/TextAreaContainer';
import ButtonForm from './ButtonForm/ButtonForm';

const FormContact = ({ sendContact }) => (
  <FormContainer onSubmitEvent={sendContact}>
    <Field
      label="Nom"
      name="lastname"
      type="text"
      placeholder="Nom"
      pattern="[\w]{1,15}"
      title="Pas de caractères spéciaux, et maximum 15 caractères"
      requiredlabel={true}
      required
    />
    <Field
      label="Prénom"
      name="firstname"
      type="text"
      placeholder="Prénom"
      pattern="[\w]{1,15}"
      title="Pas de caractères spéciaux, et maximum 15 caractères"
      requiredlabel={true}
      required
    />
    <Field
      label="Email"
      name="email"
      type="email"
      placeholder="Email"
      requiredlabel={true}
      required
    />
    <Field
      label="Objet"
      name="object"
      type="text"
      placeholder="Objet"
      pattern="[\w]{1,15}"
      title="Pas de caractères spéciaux"
      requiredlabel={true}
      required
    />
    <TextArea label="Message" name="message" requiredlabel={true} required />
    <ButtonForm type="submit">envoyer</ButtonForm>
  </FormContainer>
);

export default FormContact;
