import { connect } from 'react-redux';
import FormContact from '../../../Components/UIElement/Forms/FormContact';
import { sendContact } from '../../../Redux/actions/user.actions';

const mapState = null;

const mapDispatch = (dispatch) => ({
  sendContact: () => {
    dispatch(sendContact());
  },
});

export default connect(mapState, mapDispatch)(FormContact);
